<template lang="pug">
  div.player-cards(:class="openClass", @click="switchOpen", v-click-outside="outsideClickHandler") 
    .header Карты
    cards-list.list(:cards="playerCards" :actionEnable="true" @click="selectCard($event)")
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import CardsList from './CardsList'

export default {
  name: 'PlayerCards',
  data(){
    return {
      isOpen: false,
    }
  },
  components: {
    'cards-list': CardsList,
  },
  computed: {
    ...mapGetters(['playerCards', 'selectedCard']),
    openClass(){
      return { 'open': this.isOpen }
    },
    cardNameSlice(){
      if(this.playerCards.length<=16){
        return 10;
      } else {
        return 5;
      }
    }
  },
  methods: {
    ...mapMutations(['selectCard']), 
    switchOpen(){
      this.isOpen = !this.isOpen;
    },
    outsideClickHandler(){
      this.isOpen = false;
    } 
  }
}
</script>

<style lang="scss" scoped>
  .player-cards{
    width: 100%;
    position: fixed;
    bottom: -130px;
    z-index: 9999;
    //background: chartreuse;
    font-size: 18pt;
    transition: 0.5s;
    .list{
      padding-top: 10px;
    }    
    &.open{
      bottom: 0;
      transition: 0.5s;
    }
  }
</style>